import React, { Component } from 'react';
import Mobile from './components/Mobile';

class WidgetComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active_offer: 0,
      history_offer: 0,
      currecy_format: '${{amoddunt}}',
      data: {
        campaign_id: props.campaign._id || '',
        offers: props.isPreview ? [1] : [],
      },
      variant: props.campaign ? props.campaign.offers[0].product.variants[0] : {},
      quantity: 1,
      maxTime: this._getSeconds(),
      startTime: new Date().getTime(),
      minimized: props.minimized,
    };
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ minimized: nextProps.minimized });
  }

  componentDidMount() {
    const { conversionBearUpsell } = this.props;

    if (conversionBearUpsell && conversionBearUpsell.onLoaded && typeof conversionBearUpsell.onLoaded === 'function') {
      conversionBearUpsell.onLoaded();
    }
  }

  _getSeconds = () => {
    const { settings } = this.props;
    const { countdown_timer } = settings && settings.design && settings.design.theme || {};

    let seconds = 120;
    if (Array.isArray(countdown_timer.start_countdown_from)) {
      seconds = countdown_timer.start_countdown_from[0] * 600 + countdown_timer.start_countdown_from[1] * 60 + countdown_timer.start_countdown_from[2] * 10 + countdown_timer.start_countdown_from[3];
    }
    return seconds;
  };

  nextOffer = () => {
    const { campaign, isPreview, getHeight } = this.props;
    console.log("TCL: WidgetComponent -> nextOffer -> nextOffer")
    if (isPreview) {
      return;
    }

    const { active_offer, history_offer } = this.state;
    if (campaign.offers.length <= active_offer + 1) {
      this.setState({ history_offer: 4 }, () => {
        if (getHeight) {
          getHeight();
        }
      });
      return null;
    }

    this.setState({
      active_offer: this.state.active_offer + 1,
      history_offer: history_offer < active_offer + 1 ? active_offer + 1 : history_offer,
      startTime: new Date().getTime(),
      quantity: 1,
      variant: campaign ? campaign.offers[this.state.active_offer + 1].product.variants[this.state.active_offer + 1] : {},
    }, () => {
      if (getHeight) {
        getHeight();
      }
    });
  };

  prevOffer = () => {
    const { campaign, isPreview, getHeight } = this.props;
    const { active_offer } = this.state;
    if (isPreview) {
      return;
    }
    if (active_offer <= 0) {
      return null;
    }

    this.setState({
      active_offer: this.state.active_offer - 1,
      startTime: new Date().getTime(),
      variant: campaign ? campaign.offers[this.state.active_offer - 1].product.variants[this.state.active_offer - 1] : {},
      quantity: 1,
    }, () => {
      if (getHeight) {
        getHeight();
      }
    });
  };

  setMinimized = (status) => {
    const { setMinimized } = this.props;
    if (setMinimized) {
      setMinimized(status);
      return;
    }
    this.setState({
      minimized: status,
    });
  };

  addOffer = () => {

    const { isPreview, isDemo, campaign } = this.props;
    if (isPreview) {
      return;
    }
    const { data, quantity, variant, active_offer, currecy_format } = this.state;

    this.setState({
      data: {
        campaign_id: data.campaign_id,
        offers: [
          ...data.offers,
          isDemo ? {
            ...variant,
            product_id: variant.product_id,
            variant_id: variant.id,
            active_offer,
            quantity,
            price: currecy_format.replace(/\{\{.*?\}\}/, variant.price).replace(/<[^>]*>/g, ''),
            product_image: campaign.offers[active_offer].product.images[0].src,
          } : {
            product_id: variant.product_id,
            variant_id: variant.id,
            active_offer,
            quantity,
          },
        ],
      },
    }, () => {
      if (active_offer === campaign.offers.length - 1) {
        this.goToCheckout();
      } else {
        this.nextOffer();
      }
    });
  };

  onChangeVariant = (variant) => {
    this.setState({ variant });
  };

  onChangeQuantity = (quantity) => {

    const { campaign } = this.props;
    const { active_offer } = this.state;

    const offer = campaign.offers[active_offer];

    if (offer.limit_products && quantity > offer.limit_products_amount) {
      return null;
    }

    this.setState({ quantity });
  };

  goToCheckout = () => {
    const { goToCheckout } = this.props;

    if (goToCheckout && typeof goToCheckout === 'function') {
      goToCheckout(this.state.data);
    }
  };

  render() {
    const { settings, campaign, isMobile, isPreview } = this.props;
    const { active_offer, currecy_format, data, variant, quantity, minimized, history_offer } = this.state;

    const settings_theme = settings && settings.design && settings.design.theme || {};

    const offer = campaign.offers[active_offer];

    return (<Mobile
      isPreview={isPreview}
      settings_theme={settings_theme}
      campaign={campaign}
      offer={offer}
      active_offer={active_offer}
      currecy_format={currecy_format}
      data={data}
      goToCheckout={this.goToCheckout}
      variant={variant}
      quantity={quantity}
      onChangeVariant={this.onChangeVariant}
      onChangeQuantity={this.onChangeQuantity}
      nextOffer={this.nextOffer}
      addOffer={this.addOffer}
      prevOffer={this.prevOffer}
      maxTime={this.state.maxTime}
      startTime={this.state.startTime}
      minimized={minimized}
      setMinimized={this.setMinimized}
      isMobile={isMobile}
      history_offer={history_offer}
    />);
  }
}

export default WidgetComponent;
