import React, { Component } from 'react';
import styled from 'styled-components';
import { findIndex } from 'lodash';
import config from '../../../redux/config';

const { colors } = config;


const Container = styled.div`
    display: flex;
    background: ${(props) => props.background_color};
    width: 100%;
    padding: 1.4rem;
    font-size: 1.8rem;
    line-height: 2.2rem;
    text-align: center;
    /* margin-top: 2rem; */
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: ${(props) => props.settings_theme.product.button_text_color || '#FFFFFF'};
    font-family: ${(props) => props.settings_theme.product.font};
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  `;


const Button = ({ onClick, settings_theme, minimized }) => {
  let background_color = '#212B36';

  if (settings_theme.product.button_color) {
    background_color = settings_theme.product.button_color;
    if (settings_theme.product.button_color.indexOf('gradient_') > -1) {
      const index = findIndex(colors.gradients, [
        'name',
        settings_theme.product.button_color,
      ]);
      if (index > -1) {
        background_color = colors.gradients[index].style;
      }
    }
  }

  return (
    <Container
      id="button-send"
      settings_theme={settings_theme}
      background_color={background_color}
      onClick={() => {
        if (onClick) {
          onClick();
        }
      }}
    >
      { minimized ? settings_theme.minimized_view.minimized_button_text : settings_theme.product.button_text}
    </Container>
  );
};

export default Button;
