import React, { Component } from 'react';
import Frame, { FrameContextConsumer } from 'react-frame-component';
import styled, { StyleSheetManager } from 'styled-components';
import classNames from 'classnames';
import WidgetComponent from '../components/WidgetComponent/WidgetComponent';
import Header from './components/Head';

const WrappContent = styled.div`
  position: relative;
    .cb-widget-component{
      display: flex;
      width: 100%;
      position: absolute;
      width: 414px;
      margin: 20px;
      bottom: 0;
      right: 0;
      flex-direction: column;
      overflow: hidden;
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.5);
      border-radius: 5px;
      .cb-widget-header{
        height: 26px;
        min-height: 26px;
        background: white;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding: 0 10px;
        svg{
          cursor: pointer;
        }
      }
      .cb-widget-content{
        display: flex;
        height: 100%;
      }
    }
    &.minimized{
      .cb-widget-component{
        height: ${(props) => props.height}px;
        &.fadeInOpacity{
          opacity: 1;
          animation-name: fadeInOpacity;
          animation-timing-function: ease-in;
          animation-duration: 0.5s;
        }
      }
    }
    &.not-minimized{
      width: 100vw;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.7);
      &.fadeInColorBlack{
        animation-name: fadeInOpacity;
        animation-timing-function: ease-in;
        animation-duration: 0.5s;
      } 
      &.fadeDownColorBlack{
        animation-name: fadeDownColorBlack;
        animation-timing-function: ease-out;
        animation-duration: 0.5s;
      } 
      .cb-widget-component{
        height: calc(100vh - 40px);
        &.fadeDown{
          height: ${(props) => props.height}px;
          animation-name: fadeDown;
          animation-timing-function: ease-out;
          animation-duration: 0.5s;
        }
        &.fadeUp{
          height: calc(100vh - 40px);
          animation-name: fadeUp;
          animation-timing-function: ease-in;
          animation-duration: 0.5s;
        }
        .cb-widget-content{
          min-height: calc(100vh - 66px);
        }
      }
    }

    &.isMobile{
      .cb-widget-component{
        margin: 0px;
        width: 100vw;
        border-radius: 5px 5px 0 0;
        box-shadow: 0px -2px 4px rgba(0, 0, 0, 0.12);
      }
    }
`;


class WidgetContainer extends Component {
  constructor(props) {
    super(props);
    const settings_theme = props.settings && props.settings.design && props.settings.design.theme || {};
    const minimized = settings_theme.minimized_view && settings_theme.minimized_view.hasOwnProperty('start_minimized') ? settings_theme.minimized_view.start_minimized : true;
    this.state = {
      height: 0,
      minimized: true,
      animations: {
        fadeInOpacity: true,
        fadeUp: false,
        fadeDown: false,
        fadeInColorBlack: false,
        fadeDownColorBlack: false,
      },
    };

    this.widgetRef = React.createRef();
    this.frameContext = null;
    this.isMobile = window.mobilecheck();

    if (!minimized) {
      // setTimeout(() => {
      //   this.setMinimized(false);
      // }, 200);
      this.state.minimized = false;
      this.state.animations.fadeUp = true;
      this.state.animations.fadeInColorBlack = true;
      setTimeout(() => {
        this.state.animations.fadeUp = false;
        this.state.animations.fadeInColorBlack = false;
      }, 600);
    }

    setTimeout(() => {
      this.state.animations.fadeInOpacity = false;
    }, 2000);
  }

  setMinimized = (status) => {
    const { animations } = this.state;
    if (status) {
      this.setState({
        animations: {
          ...animations,
          fadeDown: true,
          fadeDownColorBlack: true,
        },
      }, () => {
        setTimeout(() => {
          this.setState({
            minimized: true,
            animations: {
              ...animations,
              fadeDown: false,
              fadeDownColorBlack: false,
            },
          });
        }, 498);
      });
    } else {
      this.setState({
        minimized: status,
        animations: {
          ...animations,
          fadeUp: true,
          fadeInColorBlack: true,
        },
      }, () => {
        setTimeout(() => {
          this.state.animations.fadeUp = false;
          this.state.animations.fadeInColorBlack = false;
        }, 600);
      });
    }

  };

  getHeight = () => {
    const elmnt = this.frameContext.document.getElementById('minimize-container');
    let heightComponent = 0;
    if (elmnt) {
      heightComponent = elmnt.clientHeight;
    } else {
      heightComponent = 0;
      const progressItems = this.frameContext.document.getElementById('progress-items');
      const topBar = this.frameContext.document.getElementById('top-bar');
      const countDownTimer = this.frameContext.document.getElementById('count-down-timer');
      const buttonSend = this.frameContext.document.getElementById('button-send');

      if (progressItems) {
        heightComponent += progressItems.clientHeight;
      }
      if (topBar) {
        heightComponent += topBar.clientHeight;
      }
      if (countDownTimer) {
        heightComponent += countDownTimer.clientHeight;
      }
      if (buttonSend) {
        heightComponent += buttonSend.clientHeight;
      }
    }

    this.setState({ height: heightComponent });
    if (heightComponent === 0) {
      setTimeout(() => {
        this.getHeight();
      }, 1000);
    }
  };

  render() {
    const { minimized, animations: { fadeInOpacity, fadeUp, fadeDown, fadeDownColorBlack, fadeInColorBlack }, height } = this.state;
    const { forseIsMobile, url } = this.props;

    const isMobile = typeof forseIsMobile !== undefined ? forseIsMobile : this.isMobile;
    return (
      <WrappContent height={height} className={classNames({ minimized, isMobile, 'not-minimized': !minimized, fadeInColorBlack, fadeDownColorBlack })}>
        <div className={classNames('cb-widget-component', { fadeInOpacity, fadeUp, fadeDown })}>
          {!minimized ? (
            <Header setMinimized={this.setMinimized} />
          ) : null}
          <div className="cb-widget-content">
            <Frame
              title="widget"
              id="widget"
              frameBorder="0"
              border="0"
              width="100%"
              height="100%"
              cellSpacing="0"
              scrolling="no"
              style={{ pointerEvents: 'all' }}
              contentDidMount={() => {
                this.getHeight();
              }}
              initialContent={`<!DOCTYPE html><html>
                <head>
                  <link href="https://fonts.googleapis.com/css?family=Acme|Bitter|Josefin+Slab|Lato|Montserrat|Noto+Sans|Open+Sans|Pacifico|Raleway|Roboto|Ubuntu&display=swap" rel="stylesheet" >
                  <link href="https://fonts.googleapis.com/css?family=Archivo|Asap|Barlow|Cabin|Catamaran|Didact+Gothic|Fjalla+One|Fredoka+One|Hammersmith+One|Josefin+Sans|Kalam|Lexend+Zetta|Lora|Manjari|Mansalva|Neuton|Notable|Oswald|Overpass|Ultra|Vidaloka|Vollkorn&display=swap" rel="stylesheet" >
                  <link href="${url}static/carousel.min.css" rel="stylesheet" >
                </head>
                <body>
                  <div class="frame-root"></div>
                </body>
              </html>`}
              head={[
                <style key="default_css">{`
                  
                  body,.frame-root,.frame-content{
                    margin: 0;
                    width: 100%;
                    height: 100%;
                  }
                  html {
                    box-sizing: border-box;
                    width: 100%;
                    height: 100%;
                    font-size: 10px;
                  }
                  *, *:before, *:after {
                    box-sizing: inherit;
                  }
                  @-moz-document url-prefix() {
                    select {
                      text-indent: -2px
                    }
                  }
                  .control-arrow {
                    top: calc(50% - 2.9rem) !important;
                    opacity: 1 !important;
                    width: 29px;
                    height: 40px;
                    background: rgba(0, 0, 0, 0.2) !important;
                  }

                  .control-arrow:before{
                    border-top: none !important;
                    border-bottom: none !important;
                  }
  
                  .control-arrow:before, .control-arrow:after {
                      border-right: 2px solid !important;
                      content: '' !important;
                      display: block !important;
                      height: 8px !important;
                      margin-top: -6px !important;
                      position: absolute !important;
                      -moz-transform: rotate(45deg) !important;
                      -o-transform: rotate(45deg) !important;
                      -webkit-transform: rotate(45deg) !important;
                      transform: rotate(45deg) !important;
                      right: 10px !important;
                      top: 50% !important;  
                      width: 0 !important;
                  }
                  
                  .control-prev:after {
                      margin-top: -1px !important;
                      -moz-transform: rotate(135deg) !important;
                      -o-transform: rotate(135deg) !important;
                      -webkit-transform: rotate(135deg) !important;
                      transform: rotate(135deg) !important;
                      right: 52% !important;
  
                  }
  
                  .carousel .control-next.control-arrow::before {
                      border-left: none !important;
                      -moz-transform: rotate(135deg) !important;
                      -o-transform: rotate(135deg) !important;
                      -webkit-transform: rotate(135deg) !important;
                      transform: rotate(135deg) !important;
                  }
  
                  .control-next:after{
                      margin-top: -1px !important;
                      -moz-transform: rotate(45deg) !important;
                      -o-transform: rotate(45deg) !important;
                      -webkit-transform: rotate(45deg) !important;
                      transform: rotate(45deg) !important;
                      right: 52% !important;
                  }
                  .slide {
                      padding: 0 5px !important;
                      background: none !important;
                  }

                  .slider div{
                    margin: 0 !important;
                  }

                  .slide:first-child {
                    padding-left: 1rem !important;
                  }

                  .slide:last-child {
                    padding-right: 1rem !important;
                  }
  
                  .control-arrow:hover {
                    opacity: 1 !important;
                  }
                `}
                </style>,
              ]}
            >
              <FrameContextConsumer>
                {
                  (frameContext) => {
                    this.frameContext = frameContext;
                    return (
                      <StyleSheetManager target={frameContext.document.head}>
                        <WidgetComponent
                          {...this.props}
                          minimized={minimized}
                          setMinimized={this.setMinimized}
                          isMobile={isMobile}
                          getHeight={this.getHeight}
                        />
                      </StyleSheetManager>
                    );
                  }
                }
              </FrameContextConsumer>
            </Frame>
          </div>
        </div>
      </WrappContent>
    );
  }
}

export default WidgetContainer;
