import React, { Component } from 'react';
import { findIndex } from 'lodash';
import styled from 'styled-components';
import config from '../../../redux/config';
import NextOfferMobile from './NextOfferMobile';

const { colors } = config;

const Container = styled.div`
  display: flex;
  /* height: 12rem; */
  /* padding: 1.6rem; */
  background: ${(props) => props.background_color};
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 0.2rem;
  padding: 0.9rem 1rem;
  position: relative;
  margin: 0 1rem;
  margin-bottom: ${(props) => (props.minimized ? '1rem' : 0)};
`;

const ContainerText = styled.div`
  color: ${(props) => props.countdown_timer.text_color};
  font-size: ${(props) => props.countdown_timer.text_size / 10}rem;
  line-height: ${(props) => (parseInt(props.countdown_timer.text_size, 10) + 3) / 10}rem;
  font-family: ${(props) => props.countdown_timer.font};
  text-align: center;
`;

class CountdownTimer extends Component {
  constructor(props) {
    super(props);

    const { startTime, maxTime } = props;

    this.state = {
      seconds: maxTime - Math.round((new Date().getTime() - startTime) / 1000) || 120,
      startTime,
      maxTime,
    };

    this.timer = null;
  }

  componentWillReceiveProps(nextProps) {
    const { startTime, maxTime } = nextProps;
    if (nextProps.active_offer !== this.props.active_offer) {
      this.setState({
        seconds: maxTime - Math.round((new Date().getTime() - startTime) / 1000) || 120,
        startTime,
        maxTime,
      }, () => {
        this.startInterval();
      });
    }
  }

  componentDidMount() {
    const { isPreview } = this.props;
    if (isPreview) {
      return null;
    }
    this.startInterval();
  }

  startInterval = () => {
    this.timer = setInterval(() => {
      const { startTime, maxTime } = this.state;

      const seconds = maxTime - Math.round((new Date().getTime() - startTime) / 1000);
      if (seconds <= 0) {
        this.setState({ seconds: 0 });
        clearInterval(this.timer);
        return;
      }
      if (this.state.seconds) {
        this.setState({ seconds: maxTime - Math.round((new Date().getTime() - startTime) / 1000) });
      }
    }, 1000);
  };

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  _getTimes = () => {
    const minutes = Math.floor(this.state.seconds / 60);
    const seconds = this.state.seconds - minutes * 60;
    return {
      minutes_10: minutes > 10 ? Math.floor(minutes / 10) : 0,
      minutes: minutes > 10 ? minutes - Math.floor(minutes / 10) * 10 : minutes,
      seconds_10: seconds > 10 ? Math.floor(seconds / 10) : 0,
      seconds: seconds > 10 ? seconds - Math.floor(seconds / 10) * 10 : seconds,
    };
  };


  render() {
    const { settings_theme: { countdown_timer }, history_offer, active_offer, minimized } = this.props;

    if (!countdown_timer.show) {
      return null;
    }

    if (history_offer > active_offer) {
      return null;
    }

    let background_color = 'linear-gradient(180deg, #4200FF 0%, #AD00FF 100%)';

    if (countdown_timer.background_color) {
      background_color = countdown_timer.background_color;
      if (countdown_timer.background_color.indexOf('gradient_') > -1) {
        const index = findIndex(colors.gradients, [
          'name',
          countdown_timer.background_color,
        ]);
        if (index > -1) {
          background_color = colors.gradients[index].style;
        }
      }
    }

    const timers = this._getTimes();

    return (
      <Container background_color={background_color} id="count-down-timer" minimized={minimized}>
        <NextOfferMobile {...this.props} />
        <ContainerText countdown_timer={countdown_timer} >{countdown_timer.bar_text} {timers.minutes_10}{timers.minutes}:{timers.seconds_10}{timers.seconds}</ContainerText>
      </Container>
    );
  }
}

export default CountdownTimer;
