import React, { Component } from 'react';
import styled from 'styled-components';

const DivProgres = styled.div`
  display: flex;
  background: green;
  flex: 1;
  margin: 0 0.4rem;
  background: rgba(255, 255, 255, 0.6);
  /* border-radius: 0.2rem; */
  height: 0.4rem;
  -webkit-clip-path: inset(15% 10% 15% 10% round 2px);
  clip-path: inset(0% 0% 0% 0% round 2px);
`;

const Indicator = styled.div`
  width: ${(props) => (props.progress ? `${props.progress}%` : '0%')};
  height: 0.4rem;
  background: white;
  /* border-radius: 0.2rem; */
  -webkit-clip-path: inset(15% 10% 15% 10% round 2px);
  clip-path: inset(0% 0% 0% 0% round 2px);
`;

const ProgresItem = ({ progress, onClick }) => (
  <DivProgres onClick={onClick}>
    <Indicator progress={progress} />
  </DivProgres>
);

class ProgressItems extends Component {
  constructor(props) {
    super(props);

    const { maxTime, startTime, history_offer, active_offer, settings_theme } = props;
    this.state = {
      progress: settings_theme.countdown_timer.show ? (new Date().getTime() - startTime) * 100 / (maxTime * 1000) : 0,
      maxTime,
      startTime,
    };

    if (history_offer == active_offer && settings_theme.countdown_timer.show) {
      this.startInterval();
    }
  }

  startInterval = () => {
    if (this.interval) {
      clearInterval(this.interval);
    }

    this.interval = setInterval(() => {
      const { nextOffer } = this.props;
      const { maxTime, startTime } = this.state;

      if (new Date().getTime() - startTime >= maxTime * 1000) {
        this.state.progress = 0;
        clearInterval(this.interval);
        nextOffer();
      }

      this.setState({
        progress: (new Date().getTime() - startTime) * 100 / (maxTime * 1000),
      });
    }, 200);
  };

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  componentWillReceiveProps(nextProps) {
    const { maxTime, history_offer, active_offer, settings_theme } = nextProps;

    if (!settings_theme.countdown_timer.show) {
      clearInterval(this.interval);
      return null;
    }

    if (history_offer != active_offer) {
      clearInterval(this.interval);
      return null;
    }

    if (nextProps.active_offer != this.props.active_offer) {  
      this.setState({
        progress: (new Date().getTime() - nextProps.startTime) * 100 / (maxTime * 1000),
        maxTime: nextProps.maxTime,
        startTime: nextProps.startTime,
      }, () => {
        this.startInterval();
      });
    }
  }

  render() {
    const { campaign, active_offer, history_offer, nextOffer, prevOffer, isMobile } = this.props;

    const { progress } = this.state;
    const ProgresItems = styled.div`
      display: flex;
      min-height: 2rem;
      justify-content: space-evenly;
      align-items: center;
      padding: 0 0.4rem;
      background: black;
    `;

    return (
      <ProgresItems id="progress-items">
        {campaign.offers.map((item, index) => (
          <ProgresItem
            key={`progres-${item._id + index}`}
            progress={(() => {
              if (index < active_offer) {
                return 100;
              }
              if (index > active_offer) {
                return 0;
              }
              return history_offer === index ? progress : 100;
            })()}
            onClick={() => {
              console.log('index', index, campaign.offers.length, active_offer)
              if (index === 1 && campaign.offers.length && isMobile) {
                if (active_offer > 1) {
                  if (prevOffer) {
                    prevOffer();
                  }
                } else if (active_offer < 1) {
                  if (nextOffer) {
                    nextOffer();
                  }
                }
              }
            }}
          />
        ))}
      </ProgresItems>
    );
  }
}

export default ProgressItems;
