import React, { Component } from 'react';
import styled from 'styled-components';
import ProductTitle from './ProductTitle';
import Description from './Description';
import IconShipping from './icons/IconShipping';
import Options from './Options';
import SliderImages from './SliderImages';
import NextOfferMobile from './NextOfferMobile';

const Container = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  /* padding: 1.6rem; */
`;

const DivText = styled.div`
  /* padding: 1.6rem; */
  position: relative;
  display: flex;
  flex-direction: column;
  /* text-align: center; */
  /* justify-content: center; */
`;

const OldPrice = styled.div`
  font-size: 1.6rem;
  line-height: 1.9rem;
  margin-top: 0.6rem;
  color: ${(props) => props.settings_theme.price.old_price_color || '#DE3618'};
  display: flex;
  font-family: ${(props) => props.settings_theme.price.font};
  justify-content: center;
  span{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    &::after{
      content: ' ';
      /* top: 0; */
      /* left: 0; */
      border: 0.05rem solid ${(props) => props.settings_theme.price.old_price_color || '#DE3618'};
      transform: rotate(18.43deg);
      position: absolute;
      width: 100%;
    }
  }
`;
const Price = styled.div`
  font-weight: bold;
  font-size: 2.6rem;
  line-height: 3.1rem;
  margin-top: 0.8rem;
  color: ${(props) => props.settings_theme.price.new_price_color || '#50B83C'};
  font-family: ${(props) => props.settings_theme.price.font};
  text-align: center;
`;

const YouSave = styled.div`
  font-size: 1.6rem;
  line-height: 1.9rem;
  color: ${(props) => props.settings_theme.price.new_price_color || '#50B83C'};
  margin-top: 0.3rem;
  font-family: ${(props) => props.settings_theme.price.font};
  text-align: center;
`;

const BlockShipping = styled.div`
  display: flex;
  margin-top: 0.6rem;
  justify-content: center;
`;

const FreeShipping = styled.div`
  border: 0.1rem solid ${(props) => props.settings_theme.price.text_color || '#DE3618'};
  box-sizing: border-box;
  border-radius: 0.5rem;
  padding: 0.6rem;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.4rem;
  color: ${(props) => props.settings_theme.price.text_color || '#DE3618'};
  font-family: ${(props) => props.settings_theme.price.font};
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;


class Product extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const {
      settings_theme,
      currecy_format,
      offer,
      variant,
      quantity,
      onChangeVariant,
      onChangeQuantity,
      active_offer,
      isMobile,
    } = this.props;

    const free_shipping = offer.upsell_type == 'free_shipping';

    const product = offer.product;

    const compare_at_price = !isNaN(parseFloat(variant.compare_at_price)) ? parseFloat(variant.compare_at_price) : 0;
    const price = !isNaN(parseFloat(variant.price)) ? parseFloat(variant.price) : 0;

    const discount_text = settings_theme.price.discount_text.replace('{{discount-amount}}', currecy_format.replace(/\{\{.*?\}\}/, (compare_at_price - price).toFixed(2)).replace(/<[^>]*>/g, ''));

    return (
      <Container>
        <DivText>
          <NextOfferMobile {...this.props} />
          <ProductTitle product={product} settings_theme={settings_theme} />
          {!free_shipping ? (
            <React.Fragment>
              <OldPrice settings_theme={settings_theme}>
                <span style={{ position: 'relative' }}>
                  {currecy_format.replace(/\{\{.*?\}\}/, variant.compare_at_price).replace(/<[^>]*>/g, '')}
                </span>
              </OldPrice>
              <Price settings_theme={settings_theme}>{currecy_format.replace(/\{\{.*?\}\}/, variant.price).replace(/<[^>]*>/g, '')}</Price>
              <YouSave settings_theme={settings_theme}>{discount_text}</YouSave>
            </React.Fragment>) : (
              <React.Fragment>
                <Price settings_theme={settings_theme}>{currecy_format.replace(/\{\{.*?\}\}/, variant.price).replace(/<[^>]*>/g, '')}</Price>
                <BlockShipping settings_theme={settings_theme}>
                  <FreeShipping settings_theme={settings_theme}>
                    <IconShipping style={{ marginRight: '0.8rem', fill: settings_theme.price.text_color }} fill={settings_theme.price.text_color} /> {settings_theme.price.text}
                  </FreeShipping>
                </BlockShipping>
              </React.Fragment>)}
        </DivText>
        <SliderImages images={product.images.map((item) => item.src)} isMobile={isMobile} active_offer={active_offer} />
        <Options
          variant={variant}
          quantity={quantity}
          product={product}
          settings_theme={settings_theme}
          onChangeVariant={onChangeVariant}
          onChangeQuantity={onChangeQuantity}
        />
        <DivText>
          <Description product={product} settings_theme={settings_theme} />
        </DivText>
      </Container>
    );
  }
}

export default Product;
