import React, { Component } from 'react';
import styled from 'styled-components';
import ArrowLeft from './icons/ArrowLeft';
import ArrowRight from './icons/ArrowRight';

class TopNavigation extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { campaign, active_offer, settings_theme, nextOffer, prevOffer, isPreview } = this.props;

    if (campaign.offers.length === 1) {
      return null;
    }

    const Container = styled.div`
      display: flex;
      background: black;
      flex-direction: column;
    `;

    const Nav = styled.div`
      display: flex;
      justify-content: space-between;
      padding: 1.1rem 1rem;
    `;

    const Left = styled.div`
      color: white;
      display: flex;
      align-items: center;
      font-size: 1.5rem;
      line-height: 1.8rem;
      cursor: pointer;
      font-family: 'Open Sans', sans-serif;
    `;

    const Right = styled.div`
      color: white;       
      display: flex;
      align-items: center;
      font-size: 1.5rem;
      line-height: 1.8rem;
      cursor: pointer;
      font-family: 'Open Sans', sans-serif;
    `;

    return (
      <Container id="top-navigation">
        <Nav>
          {active_offer !== 0 || isPreview ? (
            <Left
              onClick={() => {
                prevOffer();
              }}
            >
              <ArrowLeft style={{ width: '1.8rem', height: '1.7rem' }} /> {settings_theme.upsell_page.previous_offer_text || ''}
            </Left>) : <div />}
          {campaign.offers.length !== active_offer + 1 ? (
            <Right
              onClick={() => {
                nextOffer();
              }}
            >
              {settings_theme.upsell_page.next_offer_text || ''} <ArrowRight style={{ width: '1.8rem', height: '1.7rem' }} />
            </Right>) : null}
        </Nav>
      </Container>
    );
  }
}

export default TopNavigation;
