import React from 'react';
import styled from 'styled-components';

const Link = ({ settings_theme, data, goToCheckout }) => {

  if (!data.offers.length) {
    return null;
  }

  const Container = styled.div`
    width: 100%;
    font-size: 1.6rem;
    line-height: 1.9rem;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    text-decoration-line: underline;
    cursor: pointer;
    background-color: ${settings_theme.product.go_to_checkout_bg_color || '#FFFFFF'};
    color: ${settings_theme.product.go_to_checkout_text_color || '#212B36'};
    font-family: ${settings_theme.product.font};
    padding: 1rem;
  `;

  return (
    <Container
      onClick={() => {
        if (goToCheckout) {
          goToCheckout();
        }
      }}
    >
      {settings_theme.product.go_to_checkout_text} ({data.offers.length})
    </Container>
  );
};

export default Link;
